import React, { useState } from "react";
import { Chess } from "chess.js";
import { Chessboard } from "react-chessboard";

function App() {
  const [game, setGame] = useState(new Chess());
  const [history, setHistory] = useState([]);
  const [gameOver, setGameOver] = useState(false);

  function safeGameMutate(modify) {
    const updated = new Chess(game.fen());
    modify(updated);
    setGame(updated);
    setHistory(updated.history()); // keeps full history
    if (updated.isGameOver()) setGameOver(true);
  }

  async function onDrop(sourceSquare, targetSquare) {
    if (gameOver) return false;

    let move = null;
    safeGameMutate((g) => {
      move = g.move({
        from: sourceSquare,
        to: targetSquare,
        promotion: "q",
      });
    });

    if (move === null) return false;

    //  Only request AI move if it's now Black's turn
    if (game.turn() === "b" && !game.isGameOver()) {
      await getAIMove();
    }

    return true;
  }

  async function getAIMove() {
    try {
      const response = await fetch("/api/ai-move", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ fen: game.fen() }),
      });
      const data = await response.json();

      if (data.move) {
        safeGameMutate((g) => {
          g.move({
            from: data.move.from,
            to: data.move.to,
            promotion: data.move.promotion || "q",
          });
        });
      } else {
        console.warn("AI did not return a move", data);
      }
    } catch (error) {
      console.error("Error fetching AI move:", error);
    }
  }

  function newGame() {
    const freshGame = new Chess();
    setGame(freshGame);
    setHistory([]);
    setGameOver(false);
  }

  return (
    <div style={{ backgroundColor: "#3c9959", height: "100vh", padding: "20px" }}>
      <h1 style={{ color: "white" }}>Chess AI App</h1>
      <button onClick={newGame}>New Game</button>
      <div style={{ display: "flex", marginTop: "20px" }}>
        <Chessboard
          position={game.fen()}
          onPieceDrop={onDrop}
          boardWidth={500}
        />
        <div style={{ marginLeft: "20px", color: "white" }}>
          <h3>Move History</h3>
          <ol>
            {history.map((move, i) => (
              <li key={i}>{move}</li>
            ))}
          </ol>
          {gameOver && <h2>Game Over</h2>}
        </div>
      </div>
    </div>
  );
}

export default App;
